const PRIMARY_DOMAIN = 'start.me'
// const PRIMARY_DOMAIN = 'beta.start.me'
// const SM_PRIMARY_LOCATION = 'http://localhost:5000'
const SM_PRIMARY_LOCATION = 'https://' + PRIMARY_DOMAIN
const SM_API_LOCATION = SM_PRIMARY_LOCATION
const EXTENSION_TYPE = 'newtab'
const CURRENT_BROWSER = 'chrome'
const CURRENT_VERSION = '7.3'
const SM_OPTION_HOMEPAGE_ID = 'homepageId'
const SM_OPTION_HOMEPAGE_URL = 'homepageUrl'
const SM_MESSAGE_SIGN_IN = 'signIn'
const SM_MESSAGE_SIGNED_IN = 'signedInToStartme'
const SM_MESSAGE_OPEN_OPTIONS_PAGE = 'openOptionsPage'
// const MIN_EXTENSION_INFO_UPDATE_DELAY = 60 * 1000 // 1 min
const MIN_EXTENSION_INFO_UPDATE_DELAY = 3 * 60 * 60 * 1000 // 3 hours

class StartmeClass {
  constructor () {
    this.MINUTE = 60 * 1000
    this.lastExtensionInfoUpdated = 0
    this.USER_FALLBACK_LOCALE = 'en_US'
  }

  /**
   * Constructs an API call URL
   * @param path
   * @returns {string}
   */
  apiURL (path = '') {
    return SM_API_LOCATION + path
  }

  /**
   * Constructs a URL to navigate to the primary start.me domain
   * @param path
   * @returns {string}
   */
  primaryURL (path = '') {
    return SM_PRIMARY_LOCATION + path
  }

  /**
   * Creates a new element.
   * @note The classes, attributes, text and child parameters are optional and can appear in random order, with the exception
   * of the text parameter, which should always occur after at least one attributes parameter
   * @param {Element} [parent]
   * @param {string} tag
   * @param {string} [classes]
   * @param {{}} [attributes]
   * @param {string} [text] Text to add to child node (only works when attributes option was specified as well)
   * @param {Element} [child]
   * @returns {Element}
   */
  crel (/* parent, tag, classes, attributes, text, child */) {
    // Parse parameters; use arguments directly rather than converting to array, to make sure V8 optimizations can take place
    let argc = arguments.length, argp = 0, tag = arguments[argp++], parent = null
    if (typeof tag == "object") {
      parent = tag
      tag = arguments[argp++]
    }

    // Create element and optionally append to parent
    let element = document.createElement(tag)

    // Iterate other parameters
    let sawAttributes = false
    while (argp <= argc) {
      let arg = arguments[argp++]
      switch (typeof arg) {
        case 'string':
          if (sawAttributes) {
            // Add a text node
            element.appendChild(document.createTextNode(arg))
          } else {
            // Add a class
            element.classList.add(arg)
          }
          break
        case 'object':
          if (arg == null) {
            // Do nothing
          } else if (arg.nodeType) {
            // Assume Node
            element.appendChild(arg)
          } else {
            // Assume attributes object
            sawAttributes = true
            for (let attr in arg) {
              element.setAttribute(attr, arg[attr])
            }
          }
          break
      }
    }

    if (parent) parent.appendChild(element)
    return element
  }

  /**
   * Converts a plain object into a form data structure
   * @param object
   * @returns {FormData}
   */
  formData (object) {
    let data = new FormData()

    for (const property in object) {
      if (object.hasOwnProperty(property)) data.set(property, object[property])
    }

    return data
  }

  /**
   * Returns the current time
   */
  now () {
    return (new Date()).valueOf()
  }

  /**
   * Returns the current user
   * @param noCache
   * @returns {Promise}
   */
  getUser (noCache) {
    return new Promise((resolve, reject) => {
      // Consult cache
      let cachedUser = false

      chrome.storage.local.get(['cachedUser'], (result) => {
        try {
          cachedUser = JSON.parse(result.cachedUser)
          if (typeof cachedUser != 'object') cachedUser = false
        } catch (e) {
          cachedUser = false
        }

        // See if we need to fetch
        let fetchNeeded = noCache || !cachedUser || (this.now() - cachedUser.timestamp > 20 * this.MINUTE)

        // See if cache is still fresh
        if (!fetchNeeded) return done(cachedUser.user)

        let url = this.apiURL('/logged_in')

        if (this.checkExtensionInfoTimestampExpired()) {
          url += `?extension_type=${EXTENSION_TYPE}&browser=${CURRENT_BROWSER}&version=${CURRENT_VERSION}`
          this.updateExtensionInfoTimestamp()
        }

        fetch(url, { credentials: 'include' })
          .then(response => {
            if (response.status != 200) return reject()

            response.json().then(data => {
              const dataToStore = JSON.stringify({
                user: data.user,
                timestamp: this.now()
              })

              // Store user data in cache
              chrome.storage.local.set({ cachedUser: dataToStore }, () => { })
              done(data.user)
            })
          })
          .catch(error => {
            // Clear cache
            this.clearCache()
            reject()
          })
      })

      function done(user) {
        if (user) resolve(user)
        else reject()
      }
    })
  }

  /**
   * Removes cached data from storage.
   */
  clearCache (options = {}) {
    chrome.storage.local.remove([
      'cachedUser',
      'cachedPages',
      'cachedUserPages',
      'backgroundImage',
      'backgroundColor',
      SM_OPTION_HOMEPAGE_URL,
    ])

    if (!options.keepHomepage) {
      chrome.storage.local.remove([SM_OPTION_HOMEPAGE_ID])
    }
  }

  /**
   * Removes cached data from storage.
   */
  clearPages () {
    chrome.storage.local.remove([
      'cachedPages',
      'cachedUserPages',
      'backgroundImage',
      'backgroundColor',
      SM_OPTION_HOMEPAGE_URL,
      SM_OPTION_HOMEPAGE_ID,
    ])
  }

  /**
   * Requires a user to be signed in. Offers the user the chance to sign in.
   * @param noCache
   */
  requireUser (noCache) {
    return new Promise((resolve, reject) => {
      this.getUser(noCache)
        .then(resolve)
        .catch((err) => {
          chrome.runtime.sendMessage({ message: SM_MESSAGE_SIGN_IN }, success => {
            if (!success) return reject()

            // Wait for signal 'signed in'
            chrome.runtime.onMessage.addListener(message => {
              if (message.message == SM_MESSAGE_SIGNED_IN) {
                // Make sure user is signed in
                this.getUser(true /* No caching */).then(resolve).catch(reject)
              }
            })
          })
        })
    })
  }

  /**
   * Indicates whether currently logged in
   * @returns {Promise}
   * @deprecated
   */
  ifLoggedIn () {
    return this.getUser()
  }

  /**
   * Attempts to create an anonymous user
   */
  createAnonymousUser () {
    return new Promise((resolve, reject) => {
      fetch(this.primaryURL(`/users/new/anonymous.json?skip_default_page=true&utm_medium=extension&utm_source=${CURRENT_BROWSER}`), {
        method: 'POST',
        credentials: 'include'
      })
      .then(response => {
        if (response.status != 201 /* Created */) return reject()
        return response.json()
      })
      .then(user => resolve(user))
      .catch(reject)
    })
  }

  /**
   * Updates the specified properties of the current user
   * @param properties
   * @returns {Promise}
   */
  updateUser (properties) {
    return new Promise((resolve, reject) => {
      // Create body
      let data = new FormData()
      for (let property in properties) {
        if (!properties.hasOwnProperty(property)) continue
        data.set(`user[${property}]`, properties[property])
      }

      // Perform update
      fetch(this.primaryURL('/users/edit.json'), {
        method: 'PUT',
        credentials: 'include',
        body: data
      })
        .then(response => {
          switch (response.status) {
            case 200:
              response.json().then(user => resolve(user)).catch(reject)
              break
            case 400: // Validation error
              response.json().then(errors => reject(errors)).catch(reject)
              break
            default:
              reject()
          }
        })
        .catch(reject)
    })
  }

  /**
   * Opens the options page
   * @returns {Promise}
   */
  openOptionsPage () {
    return new Promise((resolve, reject) => {
      chrome.runtime.sendMessage({ message: SM_MESSAGE_OPEN_OPTIONS_PAGE }, response => {
        if (response === true) resolve()
        else reject()
      })
    })
  }

  /**
   * Converts window.setTimeout to a Promiss
   * @param duration
   * @returns {Promise}
   */
  timeout (duration = 0) {
    return new Promise((resolve, reject) => {
      setTimeout(resolve, duration)
    })
  }

  /**
   * Simple string hash method. Inspired by Java's String.hashCode() method
   * (http://werxltd.com/wp/2010/05/13/javascript-implementation-of-javas-string-hashcode-method/)
   * @param s String to be hashed
   */
  hashCode (s) {
    let hash = 0, char, length = s.length
    if (length == 0) return hash
    for (let i = 0; i < length; i++) {
      char = s.charCodeAt(i)
      hash = ((hash << 5) - hash) + char
      hash &= 0x7FFFFFFF // Convert to 31bit integer to prevent sign issues
    }
    return hash
  }

  /**
   * Shuffles the items in the specified array using the Fisher–Yates algorithm
   * @param items
   */
  shuffle (items) {
    let itemCount = items.length
    for (let i = itemCount - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1))
      if (i != j) {
        let temp = items[i]
        items[i] = items[j]
        items[j] = temp
      }
    }

    return items
  }

  /**
   * Attempt to build a meaningful title from a URL
   * @param {string} url
   * @returns {string}
   */
  titleFromUrl (url) {
    // Return a cleaned up version of the URL:
    // - hostname (without www)
    // - pathname (without extension; all none alphanumerical characters removed, every word capitalized)
    let parser = this.crel('a', { href: url })
    let host = parser.hostname.replace(/^www\.([a-z]?)/, (match, cap) => { return cap.toUpperCase() }).trim()
    let path = parser.pathname.replace(/(index)?\.\w+$/, '').replace(/[^a-z0-9]+([a-z]?)/g, (match, cap) => { return ' ' + cap.toUpperCase() }).trim()

    if (path) return `${host}: ${path}`
    else return host
  }

  getLocale () {
    return chrome.i18n.getMessage('@@ui_locale').toLowerCase()
  }

  optionsOpenedInTab () {
    if (CURRENT_BROWSER == 'firefox') return true

    return chrome.runtime.getManifest().options_ui.open_in_tab
  }

  updateExtensionInfoTimestamp () {
    this.lastExtensionInfoUpdated = Date.now()
  }

  checkExtensionInfoTimestampExpired () {
    return Date.now() - this.lastExtensionInfoUpdated >= MIN_EXTENSION_INFO_UPDATE_DELAY
  }
}

const Startme = new StartmeClass()

export default Startme
