const SM_MESSAGE_GET_PAGES = 'getPages'
const SM_MESSAGE_GET_USER_PAGES = 'getUserPages'
const SM_MESSAGE_CHECK_LINKS_AUTOSAVING = 'checkLinksAutosaving'
const SM_MESSAGE_AUTOSAVE_LINK = 'autosaveLink'
const SM_MESSAGE_AUTOSAVE_LINKS = 'autosaveLinks'
const SM_MESSAGE_EDIT_AUTOSAVED_LINK = 'editAutosavedLink'
const SM_MESSAGE_CHANGE_AUTOSAVING = 'changeLinksAutosaving'
const SM_MESSAGE_DELETE_AUTOSAVED_LINK = 'removeAutosavedLink'
const SM_MESSAGE_ADD_NEW_WIDGET = 'addWidget'
const SM_MESSAGE_ADD_NEW_BOOKMARK = 'addNewBookmark'
const SM_MESSAGE_ADD_BOOKMARKS_BATCH = 'addBookmarksBatch'
const SM_MESSAGE_CHECK_LINK_EXISTS = 'checkLinkExists'

export default {
  /**
   * Get raw data of all editable pages
   * @return {Array}
   */
  getAllPages (noCache = false) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({
        message: SM_MESSAGE_GET_PAGES,
        noCache: !!noCache
      })
        .then((pages = []) => {
          resolve(pages.filter(page => !page.is_archived));
        })
    })
  },

  /**
   * Get raw data of all pages available to the user
   * @return {Array}
   */
  getAllUserPages (noCache = false) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({
        message: SM_MESSAGE_GET_USER_PAGES,
        noCache: !!noCache
      })
        .then((pages = []) => {
          resolve(pages.filter(page => !page.is_archived));
        })
    })
  },

  /**
   * Get links autosaving preference
   * @return {Array}
   */
  checkLinksAutosaving () {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_CHECK_LINKS_AUTOSAVING })
        .then(resolve)
    })
  },

  /**
   * Change links autosaving preference
   * @return {Array}
   */
  changeLinksAutosaving (requestData = {}) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_CHANGE_AUTOSAVING, requestData })
        .then(resolve)
    })
  },

  /**
   * Autosave a link
   * @return {Array}
   */
  autosaveLink (bookmarkData = {}) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_AUTOSAVE_LINK, bookmarkData })
        .then(resolve)
    })
  },

  /**
   * Autosave multiple links
   * @return {Array}
   */
  autosaveLinks (bookmarksData = []) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_AUTOSAVE_LINKS, bookmarksData })
        .then(resolve)
    })
  },

  /**
   * Edit autosaved link
   * @return {Array}
   */
  editAutosavedLink (id, bookmarkData = {}) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_EDIT_AUTOSAVED_LINK, id, bookmarkData })
        .then(resolve)
    })
  },

  /**
   * Remove autosaved link
   * @return {Array}
   */
  removeAutosavedLink (id) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_DELETE_AUTOSAVED_LINK, id })
        .then(resolve)
    })
  },

  /**
   * Add new widget
   * @param  {Object} widgetObject Widget object
   * @return {Promise}
   */
  addWidget (widgetObject) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_ADD_NEW_WIDGET, widget: widgetObject.widget, pageId: widgetObject.pageId })
        .then(resolve)
    })
  },

  /**
   * Add bookmarks
   * @param  {Number} widgetId
   * @param  {Array} bookmarks
   * @return {Promise}
   */
  addBookmarksBatch (widgetId, bookmarks) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_ADD_BOOKMARKS_BATCH, widgetId, bookmarks })
        .then((data) => {
          resolve(data)
        })
    })
  },

  /**
   * Add bookmark
   * @param  {Number} widgetId
   * @param  {Object} bookmark
   * @return {Promise}
   */
  addNewBookmark (widgetId, bookmark) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_ADD_NEW_BOOKMARK, widgetId, bookmark })
        .then(resolve)
    })
  },

  /**
   * Check if link already exisits in widget/group
   * @param  {Number} widgetId
   * @param  {String} groupId
   * @param  {String} url
   * @return {Promise}
   */
  checkLinkExists ({ widgetId, groupId, url }) {
    return new Promise(resolve => {
      browser.runtime.sendMessage({ message: SM_MESSAGE_CHECK_LINK_EXISTS, widgetId, groupId, url })
        .then(resolve)
    })
  },
}
