import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'
import ApiService from '../ApiService'

Vue.use(Vuex)

const initialState = {
  provide() {
    return {
      allPagesAccessTimestamp: null,
      allPages: [],
      userPages: [],
      interfaceLanguage: null,
      user: null,
      shouldCloseTabs: false,
    }
  },
}

export const store = new Vuex.Store({
  state: initialState.provide(),
  actions: {
    clearStorage: ({ commit }) => {
      commit('clearStorage')
    },

    setInterfaceLanguage: ({ commit }, locale) => {
      commit('setInterfaceLanguage', locale)
    },

    addWidget: ({ state, commit }, { widget, pageId, targetWidgetId }) => {
      return new Promise((resolve, reject) => {
        ApiService.addWidget({ widget, pageId, targetWidgetId })
          .then((data) => {
            commit('clearAllPagesAccessTimestamp')
            resolve(data)
          })
          .catch(reject)
      })
    },

    checkLinkExists: ({ commit }, requestData) => {
      return new Promise((resolve, reject) => {
        ApiService.checkLinkExists(requestData)
          .then(resolve)
          .catch(reject)
      })
    },

    checkLinksAutosaving: () => {
      return new Promise((resolve, reject) => {
        ApiService.checkLinksAutosaving()
          .then(resolve)
          .catch(reject)
      })
    },

    changeLinksAutosaving: (context, requestData) => {
      return new Promise((resolve, reject) => {
        ApiService.changeLinksAutosaving(requestData)
          .then(resolve)
          .catch(reject)
      })
    },

    autosaveLink: (context, bookmarkData) => {
      return new Promise((resolve, reject) => {
        ApiService.autosaveLink(bookmarkData)
          .then(resolve)
          .catch(reject)
      })
    },

    autosaveLinks: (context, bookmarksData) => {
      return new Promise((resolve, reject) => {
        ApiService.autosaveLinks(bookmarksData)
          .then(resolve)
          .catch(reject)
      })
    },

    editAutosavedLink: (context, { id, bookmarkData }) => {
      return new Promise((resolve, reject) => {
        ApiService.editAutosavedLink(id, bookmarkData)
          .then(resolve)
          .catch(reject)
      })
    },

    removeAutosavedLink: (context, id) => {
      return new Promise((resolve, reject) => {
        ApiService.removeAutosavedLink(id)
          .then(resolve)
          .catch(reject)
      })
    },

    addNewBookmark: (context, { widgetId, bookmark }) => {
      return new Promise((resolve, reject) => {
        ApiService.addNewBookmark(widgetId, bookmark)
          .then(resolve)
          .catch(reject)
      })
    },

    addBookmarksBatch: (context, { widgetId, bookmarks }) => {
      return new Promise((resolve, reject) => {
        ApiService.addBookmarksBatch(widgetId, bookmarks)
          .then(resolve)
          .catch(reject)
      })
    },

    clearAllPages({ commit }) {
      commit('setAllPages', [])
      commit('clearAllPagesAccessTimestamp')
    },

    fetchAllPages: ({ state, commit }, options = {}) => {
      const { isRequestForced, isCacheForced } = options

      return new Promise((resolve, reject) => {
        if (isCacheForced && state.allPages.length) {
          resolve(state.allPages)
          return
        } else if (!isRequestForced && state.allPages.length) {
          resolve(state.allPages)
          return
        }

        ApiService.getAllPages(options.noCache)
          .then((pages = []) => {
            commit('setAllPages', pages)
            commit('setAllPagesAccessTimestamp')
            resolve(pages)
          })
          .catch(reject)
      })
    },

    setUser: ({ commit }, user) => {
      commit('setUser', user)
    },

    setUserPages: ({ commit }, pages = []) => {
      commit('setUserPages', pages)
    },

    toggleCloseTabs: ({ commit }) => {
      commit('toggleCloseTabs')
    },
  },
  mutations: {
    toggleCloseTabs (state) {
      state.shouldCloseTabs = !state.shouldCloseTabs
    },
    setUserPages (state, pages) {
      state.userPages = pages
    },
    clearStorage: (state) => {
      state.allPagesAccessTimestamp = null
      state.allPages = []
      state.userPages = []
      state.interfaceLanguage = null
      state.shouldCloseTabs = false
    },
    setUser: (state, user) => {
      state.user = user
    },
    setAllPages: (state, pages) => {
      state.allPages = pages
    },
    setAllPagesAccessTimestamp: (state) => {
      state.allPagesAccessTimestamp = Date.now()
    },
    clearAllPagesAccessTimestamp: (state) => {
      state.allPagesAccessTimestamp = null
    },
    setInterfaceLanguage: (state, language) => {
      state.interfaceLanguage = language
    },
  },
  getters: {
    getShouldCloseTabs: state => state.shouldCloseTabs,

    getUserData: state => state.user,

    getUserPages: state => state.userPages,

    isDarkTheme: () => false,

    getAllPagesTimestamp: state => state.allPagesAccessTimestamp,

    getWidgetsByPage: state => (widgetType = false) => {
      const personalPages = {
        id: 'personal',
        label: 'Personal',
        value: [],
      }

      const teamsPages = {}

      state.allPages.forEach((page) => {
        let widgets = page.widgets

        if (widgetType) widgets = widgets.filter(widget => widget.widget_type === widgetType)

        widgets = widgets.map((w) => {
          if (!w.folders || !w.folders.length) {
            return {
              id: w.id,
              label: w.title || '',
              value: w.id,
            }
          }

          w.folders.sort((a, b) => a.index > b.index ? 1 : -1)

          return {
            id: w.id,
            label: w.title || '',
            value: w.folders.map((f) => ({
              id: `${w.id}_${f.id}`,
              label: f.title || '',
              value: `${w.id}_${f.id}`,
            }))
          }
        })

        const pageData = {
          id: page.id,
          publicId: page.public_id,
          label: page.title,
          value: widgets,
          isArchived: page.is_archived,
        }

        if (!Number(page.organization_id)) {
          personalPages.value.push(pageData)
        } else {
          if (teamsPages[Number(page.organization_id)]) {
            teamsPages[Number(page.organization_id)].value.push(pageData)
          } else {
            teamsPages[Number(page.organization_id)] = {
              id: Number(page.organization_id),
              label: page.organization_title,
              value: [pageData]
            }
          }
        }
      })

      return [personalPages, ...Object.values(teamsPages)]
    },
  },
  plugins: [
    createPersistedState({
      key: 'startme_newtab'
    })
  ],
})
