<script>
import vueTypes from 'vue-types'

export default {
  name: 'Loader',

  props: {
    blocking: vueTypes.bool.def(false),
    hideScrollbar: vueTypes.bool.def(false),
  },

  computed: {
    isIE () {
      return this.$featurizr && this.$featurizr.browser === 'internet-explorer'
    }
  }
}
</script>

<template>
  <div :class="['loader', { 'loader_blocking': blocking, 'loader_ie': isIE, 'loader_hide_scrollbar': hideScrollbar }]">
    <div class="loader__spinner loader__indicator" role="progressbar" v-if="blocking">
      <span class="loader__buffer" />
      <span class="loader__rect" />
    </div>
  </div>
</template>

<style lang="scss">
.loader {
  position: fixed;
  right: 10px;
  bottom: 10px;
  z-index: $z-index-loader;
  padding: 7px 10px;
  font-size: 14px;
  color: $color-white;
  text-align: center;
  background: $color-blue;
  transition: visibility 0s 0.5s, opacity 0.5s ease;

  &_blocking {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 0;
    line-height: 100vh;
    color: transparent;
    cursor: progress;
    background: transparent;
    border-radius: 0;
  }

  &_hide_scrollbar {
    right: 16px;
  }

  &__spinner {
    position: relative;
    width: 100%;
    height: 5px;
    overflow: hidden;
    transform: translateZ(0);
  }

  &__buffer,
  &__rect {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: transform 250ms ease;
    transform-origin: top left;
  }

  &__buffer {
    display: block;
    background-color: $color-dim-blue;
  }

  &__rect {
    width: 60%;
    background-color: $color-blue;
    transition: none;

    @include animate-loader();
  }

  &_ie &__rect {
    /**
    * IE-specific rect translation animations. The normal rect translations do not
    * work properly in IE 10/11 (they do however work properly in Edge) due to a
    * bug involving resetting an elements position after each animation iteration
    * (see: https://goo.gl/pquAMA). These are fallback animations that allow us to
    * gracefully degrade in these browsers.
    */

    @include animate-rect-ie();
  }
}
</style>
