import Vue from 'vue'
import VueI18n from 'vue-i18n'
import axios from 'axios'

import config from '../config/bookmarklet'
import CustomFormatter from './CustomFormatter'
import { store } from '../store'
import defaultTranslations from '../_locales/en/messages.json'

const locale = config.fallbackLocale
const loadedLanguages = [config.fallbackLocale] // our default language that is preloaded

// Always include the translations for the default language
const translations = defaultTranslations
const messages = { [locale]: translations }

Vue.use(VueI18n)

export const i18n = new VueI18n({
  locale,
  messages,
  fallbackLocale: config.fallbackLocale,
  formatter: new CustomFormatter({ locale }),
  silentTranslationWarn: true,
})

/**
 * Handler for switching to a different language
 * @param {String} lang
 */
export function setInterfaceLanguage(lang) {
  store.dispatch('setInterfaceLanguage', lang)
    .then(() => loadLanguageAsync(lang)) // Load main translation files
}

/**
 * Lazy-load a language
 *
 * @see https://kazupon.github.io/vue-i18n/guide/lazy-loading.html
 *
 * @param {String} lang
 */
function loadLanguageAsync(lang) {
  // If current lang is the requested lang to lazy load, return it
  if (!lang || i18n.locale === lang) return Promise.resolve(lang)

  // If the requested lang is already (lazy-)loaded, set it as 'active'
  if (loadedLanguages.includes(lang)) return Promise.resolve(setI18nLanguage(lang))

  // Fall back to English when requesting an unsupported language
  if (!config.supportedLocales.includes(lang)) return Promise.resolve(setI18nLanguage('en'))

  // Fetch translation files for requested lang
  return new Promise((resolve, reject) => {
    import(`../_locales/${lang}/messages.json`)
      .then((translations) => {
        if (!translations) return

        const allMessages = translations
        messages[lang] = translations

        i18n.setLocaleMessage(lang, allMessages)
        loadedLanguages.push(lang)

        resolve(setI18nLanguage(lang))
      }).catch(() => { }) // Ignore errors; we'll fall back to English
  })
}

/**
 * Set the provided lang as the active language, in vue-i18n, Axios & the DOM
 *
 * @param {String} lang
 * @return {String}
 */
function setI18nLanguage(lang) {
  i18n.locale = lang
  axios.defaults.headers.common['Accept-Language'] = lang
  document.querySelector('html').setAttribute('lang', lang)

  return lang
}
